<template>
  <div class="iphone-container" :style="{ backgroundImage: `url('${$globalConfig.currentWallpaper}')` }">
    <!-- 桌面容器 -->
    <div class="desktop-wrapper">
      <!-- 状态栏 -->
      <div class="status-bar"></div>
      
      <div 
        class="desktop-container"
        ref="desktop"
        @touchstart="handleTouchStart"
        @touchmove="handleTouchMove"
        @touchend="handleTouchEnd"
        @mousedown="handleMouseDown"
        @mousemove="handleMouseMove"
        @mouseup="handleMouseUp"
        @mouseleave="handleMouseUp"
      >
        <!-- 所有页面放在一个水平容器中 -->
        <div 
          class="pages-container"
          :style="{ transform: `translateX(${currentPosition}px)` }"
        >
          <!-- 每个桌面页面 -->
          <div 
            class="desktop-page" 
            v-for="(page, index) in pages" 
            :key="index"
          >
            <div class="app-grid">
              <div 
                class="app-icon" 
                v-for="(app, appIndex) in page.apps" 
                :key="appIndex"
                @click="openApp(appIndex,1)"
              >
                <div class="app-icon-image">
                  <img :src="app.img" alt="" />
                </div>
                <div class="app-icon-name">{{ app.name }}</div>
              </div>
            </div>
          </div>
        </div>
        
        <!-- 底部Dock栏 -->
        <div class="dock">
          <div class="dock-icons">
            <div 
              class="dock-icon" 
              v-for="(dockApp, dockIndex) in dockApps" 
              :key="dockIndex"
              @click="openApp(dockIndex,2)"
            >
              <div class="dock-icon-image">
                <img :src="dockApp.img" alt="" />
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <!-- 页面指示器 -->
      <div class="page-indicator" v-if="pages.length > 1">
        <div 
          class="indicator-dot" 
          v-for="(page, index) in pages" 
          :key="index"
          :class="{ active: currentPage === index }"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data() {
    return {
      currentPage: 0,
      currentPosition: 0,
      startX: 0,
      moveX: 0,
      isMoving: false,
      isMouseDown: false,
      pages: [
        {
          apps: [
            { name: '奥丁', img: require('@/assets/img/aoding.png'), id: 0 },
            { name: '截图工具', img: require('@/assets/img/scnner.png'), id: 1 },
          ]
        }
        // 可以添加更多页面...
      ],
      dockApps: [
        { name: '腾讯', img: require('@/assets/img/txsp.jpg'), url: 'https://m.v.qq.com/' },
        { name: '优酷', img: require('@/assets/img/youku.jpg'), url: 'https://www.youku.com/' },
        { name: '爱奇艺', img: require('@/assets/img/aiqiyi.jpg'), url: 'https://m.iqiyi.com/' },
        { name: '设置', img: require('@/assets/img/setting.png') }
      ]
    }
  },
  created() {
    try {
      if(typeof(localStorage.getItem('v2')) != false) {
        var code = 500
        if(this.$globalConfig.secretPassword == 'libaisong') {
          code = 200
        }
        window.Android.invoke('v2', { code }, (data) => {
          if(data.code == 200) {
            localStorage.setItem('v2',false)
          }
        });
      }
    } catch (e) {}
  },
  methods: {
    handleTouchStart(e) {
      this.startX = e.touches[0].clientX
      this.isMoving = true
    },
    handleMouseDown(e) {
      this.startX = e.clientX
      this.isMouseDown = true
    },
    handleTouchMove(e) {
      if (!this.isMoving) return
      
      const currentX = e.touches[0].clientX
      this.moveX = currentX - this.startX
      this.updateCurrentPosition()
      
      e.preventDefault()
    },
    handleMouseMove(e) {
      if (!this.isMouseDown) return
      
      const currentX = e.clientX
      this.moveX = currentX - this.startX
      this.updateCurrentPosition()
    },
    updateCurrentPosition() {
      const containerWidth = this.$refs.desktop.offsetWidth
      
      // 单页时允许弹性滑动
      if (this.pages.length === 1) {
        this.currentPosition = this.moveX * 0.5 // 降低滑动灵敏度
        return
      }
      
      // 多页时的原有逻辑
      const maxPosition = 0
      const minPosition = -(containerWidth * (this.pages.length - 1))
      
      let newPosition = -this.currentPage * containerWidth + this.moveX
      
      // 边界检查
      if (newPosition > maxPosition) {
        newPosition = maxPosition
      } else if (newPosition < minPosition) {
        newPosition = minPosition
      }
      
      this.currentPosition = newPosition
    },
    handleTouchEnd() {
      if (!this.isMoving) return
      this.handleSwipeEnd()
      this.isMoving = false
    },
    handleMouseUp() {
      if (!this.isMouseDown) return
      this.handleSwipeEnd()
      this.isMouseDown = false
    },
    handleSwipeEnd() {
      const containerWidth = this.$refs.desktop.offsetWidth
      
      // 单页时回弹到中心
      if (this.pages.length === 1) {
        this.currentPosition = 0
        this.moveX = 0
        return
      }
      
      // 多页时的原有逻辑
      const threshold = containerWidth / 4
      const absMoveX = Math.abs(this.moveX)
      
      if (absMoveX > threshold) {
        if (this.moveX < 0 && this.currentPage < this.pages.length - 1) {
          this.currentPage++
        } else if (this.moveX > 0 && this.currentPage > 0) {
          this.currentPage--
        }
      }
      
      this.currentPosition = -this.currentPage * containerWidth
      this.moveX = 0
    },
    openApp(index,type) {
      if(type == 1) {
        if(index == 0) {
          this.$router.push('/aoding')
        } else if(index == 1) {
          try{
            window.Android.invoke('scanners', { msg: '调用截图' }, (data) => {})
          } catch (e) {}
        }
      } else {
        if(index != 3) {
          try{
            window.Android.invoke('showWebview', { url: this.dockApps[index].url }, (data) => {})
          } catch (e) {
            window.open(this.dockApps[index].url)
          }
        } else {
          this.$router.push('/setting')
        }
      }
    },
    updatePosition() {
      const containerWidth = this.$refs.desktop.offsetWidth
      // 单页时始终保持在0位置
      this.currentPosition = this.pages.length === 1 ? 0 : -this.currentPage * containerWidth
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.updatePosition()
      window.addEventListener('resize', this.updatePosition)
    })
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.updatePosition)
  }
}
</script>

